#_newtitleArea{
	line-height: 1;
	height: 3em;
	width: 100%;
	resize: none;
	overflow: hidden;
	line-height: 1;
	border: 5px solid white; 
  -webkit-box-shadow: 
    inset 0 0 8px  rgba(0,0,0,0.1),
          0 0 16px rgba(0,0,0,0.1); 
  box-shadow: 
    inset 0 0 8px  rgba(0,0,0,0.1),
          0 0 16px rgba(0,0,0,0.1); 
  padding: 10px;
  background: rgba(255,255,255,0.5);
  margin: 0 0 10px 0;
}


